<template>
  <div class="custom-wrap">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">网站防护</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/protection/' + domain }">防护配置</el-breadcrumb-item>
        <el-breadcrumb-item>网页防篡改</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <el-row class="text-align-right">
          <el-button type="primary" @click="dialogBackupFormVisible = true">导出规则</el-button>
          <el-button type="primary" @click="dialogLoadFormVisible = true">加载规则</el-button>
          <el-button type="success" @click="onClickChangeOrder()">优先级调整</el-button>
          <a class="el-button el-button--primary"
            :href="'/#/page-tamper-proof-edit/' + domain + '/' + rule_name">新增规则</a>
          <!-- <a class="el-button el-button--primary is-plain" :href="'/#/protection/' + domain ">返回</a> -->


        </el-row>
        <div class="demo-block">
          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="rule_name" label="规则名"></el-table-column>
            <el-table-column prop="rule_detail" label="规则详情"></el-table-column>
            <el-table-column prop="cache_page_url" label="页面地址"></el-table-column>
            <el-table-column prop="rule_status" label="状态">
              <template #default="scope">
                <el-switch v-model="scope.row.status" @change="onChangeRuleStatus(scope.row)" active-value="true"
                  inactive-value="false" />
              </template>
            </el-table-column>

            <el-table-column label="操作" align="right">
              <template #default="scope">
                <el-button size="mini" @click="handleEdit(scope.row)" class="button-block" type="text">配置
                </el-button>
                <el-popover placement="top" width="160" v-model:visible="scope.row.isVisiblePopover">
                  <p>确定删除吗？</p>
                  <div style="text-align: right; margin: 0">
                    <el-button size="mini" type="text" @click="scope.row.isVisiblePopover = false">取消</el-button>
                    <el-button type="primary" size="mini" @click="handleDelete(scope.row)" :loading="loading">确定
                    </el-button>
                  </div>
                  <template #reference>
                    <el-button type="text" size="mini" @click="scope.row.isVisiblePopover = true">删除</el-button>
                  </template>
                </el-popover>
              </template>
            </el-table-column>
            <el-table-column label="优先级" align="right" v-if="!isShowOrder">
              <template #default="scope">
                <el-button type="success" class="icon iconfont iconxiangshang" circle
                  @click="onClickChangeOrderSubmit(scope.$index, scope.row, 'up')" title="上移" :loading="orderLoading" />
                <el-button type="success" class="icon iconfont iconxiangxia" circle
                  @click="onClickChangeOrderSubmit(scope.$index, scope.row, 'down')" title="下移"
                  :loading="orderLoading" />
                <el-button type="success" class="icon iconfont iconzhiding" circle
                  @click="onClickChangeOrderSubmit(scope.$index, scope.row, 'top')" title="置顶"
                  :loading="orderLoading" />
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
    <el-dialog title="加载规则" v-model="dialogLoadFormVisible" width="580px" :close-on-click-modal="false"
      @closed="dialogCloseLoad">
      <el-form :model="loadForm" :rules="rules" ref="loadForm" label-position="top">
        <el-form-item label="请以JSON格式输入配置" key="1" prop="rules">
          <el-input v-model="loadForm.rules" type="textarea" :autosize="{ minRows: 10 }" />
        </el-form-item>
      </el-form>
      <template #footer class="dialog-footer">
        <el-button @click="dialogLoadFormVisible = false">取消</el-button>
        <el-button type="primary" @click="onClickLoadSubmit('loadForm')" :loading="loading">确定</el-button>
      </template>
    </el-dialog>
    <el-dialog title="导出规则" v-model="dialogBackupFormVisible" width="800px" :close-on-click-modal="false"
      @closed="dialogCloseBackup">
      <el-table ref="multipleTableRef" :data="tableData" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" />
        <el-table-column prop="rule_name" label="规则名"></el-table-column>
        <el-table-column prop="rule_detail" label="规则详情"></el-table-column>
        <el-table-column prop="cache_page_url" label="页面地址"></el-table-column>
        <el-table-column prop="status" label="状态"></el-table-column>
      </el-table>
      <template #footer class="dialog-footer">
        <el-button @click="dialogBackupFormVisible = false">取消</el-button>
        <el-button type="primary" @click="onClickBackupSubmit()" :loading="loading">导出</el-button>
      </template>
    </el-dialog>
  </div>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";
import { useRoute } from 'vue-router'
import axios from "axios";
export default {
  mixins: [mixin],
  data() {
    return {
      loading: false,
      loadingPage: false,
      isShowOrder: true,
      orderLoading: false,
      tableData: [],
      domain: "",
      rule_name: "new",
      dialogLoadFormVisible: false,
      loadForm: {},
      dialogBackupFormVisible: false,
      multipleSelection: [],
    };
  },
  computed: {
    rules() {
      return {
        rules: [
          { required: true, message: '请输入', trigger: ["blur", "change"], },
        ],
      }
    }
  },
  mounted() {
    const route = useRoute()
    this.domain = route.params.domain;
    this.getData();
  },
  methods: {
    getData() {
      var t = this;
      var postUrl = "/waf/waf_get_web_page_tamper_proof_list";
      var oData = { domain: t.domain };
      
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loadingPage = false;
          t.tableData = response.data.message;
          t.tableData.forEach(item => {
            item.isVisiblePopover = false;
          })
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    handleEdit(data) {
      window.location.href = "/#/page-tamper-proof-edit/" + this.domain + '/' + data.rule_name;
    },
    handleDelete(data) {
      var t = this;
      t.loading = true;
      var postUrl = "/waf/waf_del_web_page_tamper_proof";
      var oData = {
        domain: t.domain,
        rule_name: data.rule_name,
      };   
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          data.isVisiblePopover = false;
          t.loading = false;
          t.getData();
        },
        function () {
          t.loading = false;
        }
      );
    },
    onClickChangeOrder() { 
      var t = this;
      t.isShowOrder = !t.isShowOrder;
    },
    onClickChangeOrderSubmit(index, value, operate) { 
      var t = this;
      var oData = {
        domain: t.domain,
        rule_name: value.rule_name,
      };
      if (index > 0) { 
        if (operate == "top") {
          oData.type = "top";
        }
        if (operate == "up") {
          oData.type = "exchange";
          oData.exchange_rule_name = t.tableData[index - 1].rule_name;
        }
      }

      if (index < t.tableData.length - 1) { 
        if (operate == "down") {
          oData.type = "exchange";
          oData.exchange_rule_name = t.tableData[index + 1].rule_name;
        }
      }
      var postUrl = "/waf/waf_exchange_web_page_tamper_proof_priority";
      if (oData.type == "top" || oData.type == "exchange") { 
        t.orderLoading = true;  
        JXAjax(
          "post",
          postUrl,
          oData,
          function (response) {
            t.orderLoading = false;
            t.getData();
          },
          function () {
            t.orderLoading = false;
          },
          "no-message"
        );
      }
      
    },
    onChangeRuleStatus(value) { 
      var t = this;
      var oData = {
        domain: t.domain,
        rule_name: value.rule_name,
        status: value.status
      }
      var postUrl = "/waf/waf_edit_web_page_tamper_proof_status";
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.getData();
        },
        function () {
        },
        "no-message"
      );
    },
    dialogCloseLoad() {
      this.loadForm = {};
      this.$refs["loadForm"].resetFields();
    },
    dialogCloseBackup() {
      this.$refs.multipleTableRef.clearSelection();
    },
    onClickLoadSubmit(loadForm) {
      var t = this;
      var postUrl = "/waf/waf_load_web_page_tamper_proof";
      var postData = {
        domain: t.domain,
        rules: JSON.parse(t.loadForm.rules)
      }
      t.$refs[loadForm].validate((valid) => {
        t.loading = true;
        if (valid) {
          JXAjax(
            "post",
            postUrl,
            postData,
            function (response) {
              t.dialogLoadFormVisible = false;
              t.loading = false;
              t.getData();
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
    onClickBackupSubmit() {
      var t = this;
      var postUrl = "/waf/waf_backup_web_page_tamper_proof";
      var postData = {
        domain: t.domain,
        rule_name_list: []
      };
      if (t.multipleSelection.length > 0) {
        t.loading = true;
        t.multipleSelection.forEach(item => {
          postData.rule_name_list.push(item.rule_name)
        });

        axios({
          url: postUrl,
          data: postData,
          method: 'post',
          responseType: 'blob',
        }).then((response) => {
          var fileURL = window.URL.createObjectURL(new Blob([response.data]));
          var fileLink = document.createElement('a');

          fileLink.href = fileURL;
          fileLink.setAttribute('download', 'web_page_tamper_proof.json');
          document.body.appendChild(fileLink);

          fileLink.click();
          t.loading = false;
          t.dialogBackupFormVisible = false;
        });
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
  },
};
</script>

<style>
.custom-wrap {
  /* max-width: 800px;
  min-width: 400px; */
}
.el-button.button-block {
  display: block;
  margin-left: 0px;
  text-align: right;
  width: 100%;
}

.custom-wrap .rule-matchs-content .match-box-title {
  display: inline-block;
  text-align: left;
  font-size: 14px;
  color: #909399;
  padding: 0 5px;
  box-sizing: border-box;
}
.custom-wrap .rule-matchs-content .match-box-title:before {
  content: "*";
  color: #f56c6c;
  margin-right: 4px;
}
.custom-wrap .match-inline-block {
  width: 192px;
}
.custom-wrap .match-inline-block-small {
  width: 150px;
}
.box-card-rule {
  margin-bottom: 22px;
  border: 1px solid #409eff;
}
.box-card-rule .card-item {
  border-bottom: 1px solid #e0e3e9;
  margin-bottom: 20px;
}
</style>
